import Root from "../dashboard/AdminDashCreateTags";
import { createContext } from "react";
import Middleware from '../dashboard/auth/Middleware'
import { useState, useEffect } from "react";
import AuthentVerifPage from '../dashboard/auth/AuthentVerifPage';

import { ls, ss } from '../utils/store';

import ErrorLoadPage from '../dashboard/auth/ErrorLoadPage';

export const AuthContext = createContext();

function DashboardCreateTagsRoute(props){

    const cookies = props.cookies;

    // J'utilise un middleware pour grer la valeur du token, ce dernier lorsqu'il est appel communique avec le back-office et vrifie la cohrence du token.
    // Si le token est eronn l'utilisateur est pouss hors du composant. Inversement s'il est valid l'utilisateur est pouss dedans.
    const userData = Middleware.Execute();
    const [isAuthenticated, setIsAuthenticated] = useState(null);
    useEffect(() => {
        try {
            setIsAuthenticated(userData);
        }
        catch(error){
            console.error(error);
        }
    }, [userData]);

    // Cette fonction est appel quand le bouton de connexion est appuy est qu'elle aboutit. Dans la cas cheant, je force le contenu
    // du composant en cours "Auth"  se charger une fois tout rendu termin. De cette manire je peux conserver hors contexte la vrification de l'utilisateur.
    const login = () => {
        window.location.reload();
    };

    try {
        return (
            <div className="h-full w-full">
                <AuthContext.Provider value={{ isAuthenticated, login }}>
                { !isAuthenticated ? (
                        <AuthentVerifPage />
                    ) : (
                        <Root />
                    )}
                </AuthContext.Provider>
            </div>
        );
    }
    catch(error){
        return <ErrorLoadPage error={error}/>;
    }
}

export default DashboardCreateTagsRoute;